.header {
  background: linear-gradient(-45deg, #090979, #002d62, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 20s ease infinite;
  margin-top: 30px;
  height: 80vh;
 
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes rain {
  0% {
    background-position: 10 0;
  }
  100% {
    background-position: 20% 80%;
  }
}

.footer {
  background: #001f3f;
  /* color: #001f3f; */
  text-align: center;
  padding: 1rem;
  border-color: red;
}
.footer_image {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}
.footer_image > div {
  margin: 0 1rem;
}

.footer_image > div > img {
  max-height: 13vh;
  max-width: 13vw;
}
.footer_maindiv {
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
}
.footer_maindiv > div {
  border: none;
  background-color: none;
  margin: 10px;
  transition: width 2s height 2s;
}
.footer_maindiv > div :hover {
  width: 60px;
  height: 60px;
  fill: rgb(55, 55, 169);
}

.sub_header > h1 {
  font-size: 4.5rem;
  text-shadow: 6px 4px 6px rgba(0, 0, 0, 0.6);
}
.sub_header > p {
  font-size: 1.5rem;
  text-shadow: 6px 4px 6px rgba(0, 0, 0, 0.6);
}
.sub_header > button {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  padding: 1rem 2rem;
  margin: 2rem;
  font-size: 1.5rem;
}
.sub_header > div > a {
  margin: 0.5rem;
}
.sub_header > div {
  padding: 1.5rem;
}
.sub_header > div > a > svg:hover {
  fill: #151bd6;
}
